// Variables
//
// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.

// Color system
$gray-100: $light-100;
$gray-200: $light-200;
$gray-300: $light-300;
$gray-400: $light-400;
$gray-500: $light-500;
$gray-600: $light-600;
$gray-700: $light-700;
$gray-800: $light-800;
$gray-900: $light-900;

$grays: (
);
$grays: map-merge(("100": $gray-100,
      "200": $gray-200,
      "300": $gray-300,
      "400": $gray-400,
      "500": $gray-500,
      "600": $gray-600,
      "700": $gray-700,
      "800": $gray-800,
      "900": $gray-900 ),
    $grays);

$primary: $accent-color;
$secondary: $accent-light;
$secondary-light: $base-text;

$theme-colors: (
);
// stylelint-disable-next-line scss/dollar-variable-default
$theme-colors: map-merge(("primary": $primary,
      "secondary": $secondary,
      "success": $success,
      "info": $info,
      "warning": $warning,
      "danger": $danger,
      "dark": $base-dark,
      "gray": $light-500,
      "light": $light-200,
      "lighter": $lighter ),
    $theme-colors);

$component-active-color: $white;
$component-active-bg: $accent-light;

// // Set a specific color jumpsd
$theme-color-interval: 8%;
$yiq-contrasted-threshold: 190;

$yiq-text-dark: $darker;
$yiq-text-light: $white;


// // Link
// //

$link-decoration: none;
$link-hover-decoration: underline;

$emphasized-link-hover-darken-percentage: 15%;

// // // Paragraphs
// // //

$paragraph-margin-bottom: $para-margin;


// // Grid breakpoints
// //

$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1540px
);

// @include _assert-ascending($grid-breakpoints, "$grid-breakpoints");
// @include _assert-starts-at-zero($grid-breakpoints, "$grid-breakpoints");


// // Grid containers
// //
// // Define the maximum width of `.container` for different screen sizes.

$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
  xxl: 1440px
);

// @include _assert-ascending($container-max-widths, "$container-max-widths");


// Grid columns
//
// Set the number of columns and specify the width of the gutters.

$grid-gutter-width: $gutter-width;



// Spacing
//
// Control the default styling of most Bootstrap elements by modifying these
// variables. Mostly focused on spacing.
// You can add more entries to the $spacers map, should you need more variation.

$spacers: (
);
$spacers: map-merge((0: 0,
      1: ($spacer * .375),
      2: ($spacer * .75),
      3: $spacer,
      4: ($spacer * 1.5),
      5: ($spacer * 2.75),
      'gs': $gutter-width // 0: 0,
      // 1: ($spacer * .25),
      // 2: ($spacer * .5),
      // 3: $spacer,
      // 4: ($spacer * 1.5),
      // 5: ($spacer * 3)
    ),
    $spacers);

// // Components
