   .obj1 {
       // background-color: #F0F6D5 !important;
       box-shadow: 2px 2px 5px rgb(212, 226, 150) !important;
       cursor: pointer;
   }

   .obj2 {
       // background-color: #EDFAFF !important;
       box-shadow: 2px 2px 5px rgb(177, 221, 239) !important;
       cursor: pointer;
   }


   a {
       color: $secondaryGreen;
   }

   .mint-link {
       color: $secondaryGreen;

       &:hover {
           cursor: pointer;
           color: $darkGreen;
       }
   }


   .rupee {
       background-image: url("https://d3etfstcuyoos5.cloudfront.net/app-images/imgs/rupee.webp");
       padding-left: 25px;
       background-size: 11px;
       background-repeat: no-repeat;
       background-position-y: 11px;
       background-position-x: 10px;
   }

   .solidCircleNumber {
       width: 24px;
       height: 24px;
       background-color: $darkGreen;
       color: $primaryGreen;
       font-size: 14px;
       font-weight: 700;
       border-radius: 50%;
       display: flex;
       align-items: center;
       justify-content: center;
       z-index: 10;
   }

   .formProgressBar {
       position: relative;
       top: -1.25rem;
       left: -2.25rem;
       margin-right: -4.5rem;
   }

   @media (min-width: 576px) {
       .formProgressBar {
           top: -1.5rem;
           left: -2.5rem;
           margin-right: -5rem;
       }
   }

   .normalInput {
       outline: none;
       border: none;
       border-bottom: 1px solid var(--light);
       margin-top: 2%;
       margin-bottom: 4%;
       font-size: 24px;
       font-weight: 500;
       width: 100%;
       caret-color: var(--secondaryGreen);
       background: transparent;
   }

   .normalInput:focus {
       border-bottom: 1px solid var(--secondaryGreen);
   }

   .normalInput::placeholder {
       font-size: 24px;
       font-weight: 500;
       color: var(--silver);
   }

   .normalInput.rupee {
       background-image: url("https://d3etfstcuyoos5.cloudfront.net/app-images/imgs/rupee.webp");
       padding-left: 20px;
       background-size: 16px;
       background-repeat: no-repeat;
       background-position-y: 9px;
   }

   .normalInput:disabled {
       color: var(--black);
       background-color: transparent
   }

   .eQmQXs {
       z-index: 21;
   }

   .lmpcCT {
       padding: 15px !important;
   }

   .color.color {
       border-radius: 4px;
       -moz-border-radius: 4px;
       -webkit-border-radius: 4px;
       // font-size: 15px;
       // font-weight: 600;
       // padding: 10px 20px 10px 5px;
       border: 1px solid #e5e5e5 !important;
       // border: none;
       // box-sizing: border-box;
       // box-shadow: none;
       // font-family: Open Sans, sans-serif;
   }

   .slide-in {
       animation-duration: 0.5s;
       animation-name: slidein;
       animation-fill-mode: forwards;
   }

   @keyframes slidein {
       from {
           margin-left: 100%;
           // width: 300%;
       }

       to {
           margin-left: 0%;
           // width: 100%;
       }
   }

   .form-control {
       box-shadow: none !important;
       border: 1px solid #e5e5e5 !important;

       &-focus {
           box-shadow: none !important;
           border: 1px solid #e5e5e5 !important;
       }
   }

   .fixedFullHeight {
       // 100 - header - padding - headSection
       height: calc(100vh - (#{$header-height} + 64px + 64px));
   }

   .screener .rdt_Table {
       margin: 0;
       border: 0;
   }

   .screener .dataTables_wrapper {
       display: flex;
       flex-direction: column;
       height: 100%;
   }

   .screener .rdt_TableCol {
       justify-content: end;
   }

   .screener .rdt_TableCol[data-column-id="1"] {
       justify-content: start;
   }

   //checkbox
   .screener .rdt_TableCol:first-child {
       justify-content: center;
   }

   .screener .rdt_TableCol .rdt_TableCol_Sortable {
       justify-content: end !important;
   }

   .screener .rdt_TableCol .rdt_TableCol_Sortable[data-column-id="1"] {
       justify-content: space-between !important;
   }

   .screener .rdt_TableCol .rdt_TableCol_Sortable .lnndaO {
       padding-right: 12px;
   }


   .screener .fSQziN {
       flex: 1;
       overflow: auto !important;
       border-top: 1px solid #e5e5e5;
   }

   .fSQziN::-webkit-scrollbar {
       -webkit-appearance: none;
       width: 7px;
       height: 7px;
   }

   .fSQziN::-webkit-scrollbar-thumb {
       border-radius: 4px;
       background-color: rgba(0, 0, 0, .5);
       -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, .5);
   }

   .alwaysShowScrollbar::-webkit-scrollbar {
       -webkit-appearance: none;
       width: 7px;
       height: 7px;
   }

   .alwaysShowScrollbar::-webkit-scrollbar-thumb {
       border-radius: 4px;
       background-color: rgba(0, 0, 0, .5);
       -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, .5);
   }

   .screener .rdt_TableHead {
       position: sticky;
       top: 0;
       z-index: 2;
   }

   .screener .accordion-item .title {
       font-size: 14px;
   }

   .accordion-head {
       padding: 1rem !important;
   }

   .accordion-item:not(:last-child) .accordion-head {
       border-bottom: none !important;
   }

   .accordion-item:not(:first-child) .accordion-head {
       border-top: 1px solid #e5e5e5;
   }

   .accordion-item:last-child .accordion-inner {
       border-top: none !important;
   }

   .noUi-target {
       margin-bottom: 50px;
   }