.MarketingAccordion {
    background-color: #eee;
    color: #444;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
    transition: 0.4s;
}

/* .active, */
.MarketingAccordion:hover {
    background-color: #ccc;
}

.MxPanel {
    padding: 0 18px;
    display: none;
    background-color: white;
    overflow: hidden;
}


.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px 192px 1.6px 192px;
    position: absolute;
    z-index: 1;
    width: 100vw;
    background-color: #285E4D;
    -webkit-transform: translate3d(0, 0, 0);
}

.navbar-sticky {
    background: #285E4D;
    position: fixed;
    top: 0;
    left: 0;
    box-shadow: 0.5px 0.5px 0.5px #e5e5e5;
    /* transition: box-shadow 2s ease; */
    animation: moveDown 0.5s ease-in-out;
}

.navbar--logo {
    width: 108px;
    height: 32px;
    cursor: pointer;
    /* animation: rotate 0.7s ease-in-out 0.5s; */
}

.navbar--logo2 {
    width: 160px;
    height: 32px;
    cursor: pointer;
    /* animation: rotate 0.7s ease-in-out 0.5s; */
}

.navbar--link {
    display: flex;
    list-style: none;
    align-items: center;

}

.navbar--link-item {
    margin: 0 0 0 20px;
    padding: 0 4px;
    color: #C2DC55;
    cursor: pointer;
    font-size: 14px;
    text-decoration: none;
}

.navbar--link-item:hover {

    color: #C2DC55;
    cursor: pointer;

}

#btn-link:hover {
    color: #285E4D
}

@keyframes moveDown {
    from {
        transform: translateY(-5rem);
    }

    to {
        transform: translateY(0rem);
    }
}

@keyframes rotate {
    0% {
        transform: rotateY(360deg);
    }

    100% {
        transform: rotateY(0rem);
    }
}


